import React, { useEffect, useState } from'react';
import Category from '../components/Category';
import Item from '../components/Item';
import InfiniteScroll from 'react-infinite-scroll-component';
import {S} from './Product.Styles'
import { Routes, Route } from'react-router-dom';


export default function Products() {
  //  .
  const [items, setItems] = useState(
    localStorage.getItem('items')
    ? JSON.parse(localStorage.getItem('items'))
    : []
  );
  //   
  const [nowNum, setNowNum] = useState(16);
  //true =>     handleLoadMore()  
  const [hasMore, setHasMore] = useState(true)
  
  //  -> setItems  -> state  re ->   .
  useEffect(() => {
    if(localStorage.getItem('items'))
      localStorage.setItem('items', JSON.stringify(items));
  },[items]);


  //  4 ,      
  const handleLoadMore = () => {
    if(items.length > nowNum)
    setNowNum(nowNum + 4);
    else 
      setHasMore(false);  
  };

    return (
      <div className='ProductPageContainer'> 
        <Category />
        <InfiniteScroll
         dataLength={nowNum}
         next={handleLoadMore}
         hasMore={hasMore}
         loader={<h1>Loading...</h1>}
         >
        <Routes>
          <Route path='/all' element={
            <S.ImageContainer>
              {items 
                .map((item, index) => {
                  if(index < nowNum)
                    return <Item key={index} item={item} items={items} setItems={setItems}/>
              })}
            </S.ImageContainer>
          } />

          <Route path='/product' element={
            <S.ImageContainer>
              {items 
                .map((item, index) => {
                  if(item.type === 'Product' && index < nowNum)
                    return <Item key={index} item={item} items={items} setItems={setItems}/>
              })}
            </S.ImageContainer>
          } />

          <Route path='/brand' element={
            <S.ImageContainer>
              {items 
                .map((item, index) => {
                  if(item.type === 'Brand' && index < nowNum)
                    return <Item key={index} item={item} items={items} setItems={setItems}/>
              })}
            </S.ImageContainer>
          } />

          <Route path='/category' element={
            <S.ImageContainer>
              {items 
                .map((item, index) => {
                  if(item.type === 'Category' && index < nowNum)
                    return <Item key={index} item={item} items={items} setItems={setItems}/>
              })}
            </S.ImageContainer>
          } />

          <Route path='/exhibition' element={
            <S.ImageContainer>
              {items 
                .map((item, index) => {
                  if(item.type === 'Exhibition' && index < nowNum)
                    return <Item key={index} item={item} items={items} setItems={setItems}/>
              })}
            </S.ImageContainer>
          } />
        </Routes>
        </InfiniteScroll>
      </div>
    )
  }